@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Display&display=swap);
html {
  height: 100%;
  background-color: rgba(31,64,64,1);
}

body {
  margin: 0;
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(360deg, rgba(31,64,64,1) 0%, rgba(1,12,19,1) 83%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#landing {
  background: linear-gradient(10deg, rgba(0,50,70,1) 0%, rgba(61,150,160,1) 50%, rgba(125,255,255,1) 100%);
}

#app {
  background: linear-gradient(360deg, rgba(31,64,64,1) 0%, rgba(1,12,19,1) 83%);
}
.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.videoWrapper {
  position: relative;
  padding-bottom: 56.25%;
  /* 16:9 */
  padding-top: 25px;
  height: 0;
}

.videoWrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.item:hover {
  background-color: rgba(0, 0, 0, .1);
}

.clickedItem {
  background-color: rgba(0, 0, 0, .2);
}

.text-danger {
  color: #ff5262 !important;
}

.text-warning {
  color: #ffc107 !important;
}

.score-0 {
  color: #cacaca;
}

.score-1 {
  color: #0091f2;
}

.score-2 {
  color: green;
}

